<template>
  <div id="timer">
    <div class="container with-title">
      <h2 class="title">kintre-timer</h2>
      <div>
        work
        <input type="number" v-model="work"/>
      </div>
      <p>
        rest
        <input type="number" v-model="rest"/>
      </p>
      <p>
        set
        <input type="number" v-model="set"/>
      </p>
      <div>
        <button class="" v-on:click="start" v-bind:disabled="timerOn">Start</button>
        <button class="" v-on:click="reset" v-bind:disabled="!timerOn">Reset</button>
      </div>
      <div>
        <button class="" v-on:click="set30">30秒</button>
        <button class="" v-on:click="set60">60秒</button>
        <button class="" v-on:click="set120">120秒</button>
      </div>
    </div>

  </div>

</template>

<script>
export default {
  name: 'timer',
  data() {
    return {
      work: 0,
      work_bk: 0,
      rest: 0,
      rest_bk: 0,
      set: 0,
      timerOn: false,
      timerObj: null,
      audio: new Audio(require('@/assets/Countdown01-1.mp3'))
    }
  },

  methods: {
    count: function() {
      if (this.work <= 0) {
        if (this.set <= 0) {
          this.reset()
        } else if (this.rest >= 1) {
          this.rest--
        } else if (this.rest <= 0) {
          this.work = this.work_bk
          this.rest = this.rest_bk
          this.set--
        }
      } else {
        this.work--
      }
      if (this.work === 2 || this.rest === 2) {
        this.audio.play()
      }
    },

    start: function() {
      const self = this
      this.work_bk = this.work
      this.rest_bk = this.rest
      this.audio.play()
      setTimeout(() => {
        this.timerObj = setInterval(function() { self.count() }, 1000)
      }, 2000)
      this.timerOn = true
    },

    reset: function() {
      clearInterval(this.timerObj)
      this.timerOn = false
      this.work = 0
      this.rest = 0
      this.set = 0
    },

    set30: function() {
      clearInterval(this.timerObj)
      this.timerOn = false
      this.work = 30
      this.rest = 7
      this.set = 6
    },

    set60: function() {
      clearInterval(this.timerObj)
      this.timerOn = false
      this.work = 60
      this.rest = 15
      this.set = 3
    },

    set120: function() {
      clearInterval(this.timerObj)
      this.timerOn = false
      this.work = 120
      this.rest = 30
      this.set = 5
    }
  }

}
</script>
